@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,500;0,600;1,200;1,400;1,600&display=swap);
html{scroll-behavior:smooth;overflow-x:hidden}html body{padding:0;margin:0;overflow-x:hidden;font-family:'Poppins',Georgia, 'Times New Roman', Times, serif;background-color:#f7f7f7;background-color:var(--offWhite)}html *{margin:0;padding:0;box-sizing:border-box}:root{--primaryColor:  rgb(255, 111, 255);--mainWhite: #fff;--offWhite: #f7f7f7;--mainBlack: #222;--mainGrey: #ececec;--darkGrey: #cfcfcf;--mainTransition: all 0.3s linear;--mainSpacing: 3px;--lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);--darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5)}.logo{width:100px;height:100px}.navbar{height:100%;width:100vw;background:#fff;background:var(--mainWhite);box-shadow:2px 5px 3px 0px rgba(0, 0, 0, 0.5);box-shadow:var(--lightShadow)}.nav-header{display:flex;justify-content:space-between;margin:0 auto;width:100%;align-items:center}.nav-btn{background:transparent;border:none;cursor:pointer;outline:none}.nav-icon{font-size:3.5rem;margin-right:30px;color:rgb(255, 111, 255);color:var(--primaryColor)}.nav-links{height:0;overflow:hidden;width:100%;transition:all 0.3s linear;transition:var(--mainTransition);list-style-type:none}.nav-links a{display:block;text-decoration:none;padding:1rem 0;color:#222;color:var(--mainBlack);transition:all 0.3s linear;transition:var(--mainTransition);text-align:center;font-size:1rem;font-weight:600}.nav-links a:hover{color:rgb(255, 111, 255);color:var(--primaryColor)}.show-nav{height:100%}@media screen and (min-width: 768px){.nav-btn{display:none}.nav-center{max-width:1170px;margin:0 auto;display:flex;justify-content:space-around;align-items:center}.nav-links{height:auto;display:flex;margin-left:4rem}.nav-links a{margin:0 1rem;padding:0.5rem 0}}.hero{background:linear-gradient(to right, rgba(19,19,19,0.7), rgba(0,0,0,0.7)),url(/static/media/public-speaking-3926344_1920.f8ee57fc.jpg);width:100vw;height:85vh;padding-bottom:20px;background-repeat:no-repeat;overflow:hidden;color:#e8e5e5;display:flex;flex-direction:column;align-items:center;justify-content:center;background-position:center;background-size:cover;margin-bottom:20px}@media screen and (max-width: 768px){.hero-h{font-size:40px !important}}.btn{border:0;outline:none;padding:15px 20px;background-color:rgb(255, 111, 255);background-color:var(--primaryColor);cursor:pointer;margin-top:20px;font-size:20px;color:white}.btn:hover{width:230px}.hero-header-text{width:80%}.contacts{display:flex;justify-content:space-between;align-items:center;width:80%;margin:0 auto}.exco{display:flex;flex-direction:column;align-items:center;justify-content:center;margin:20px auto}.exco-inner{padding:10px}.exco-inner a{margin-right:10px;margin-top:10px;transition:all 0.3s linear;transition:var(--mainTransition);color:rgb(255, 111, 255);color:var(--primaryColor);opacity:1 !important}.exco-inner a:hover{color:rgb(255, 111, 255);color:var(--primaryColor)}.exco-text{background-color:white;opacity:0.8;margin-top:-6px;padding:5px;line-height:2rem;transition:all 0.3s linear;transition:var(--mainTransition)}.exco-profile{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 300px));width:90vw;grid-gap:20px;align-items:center;justify-content:center;margin:0 auto}.profileImg{width:100%;height:300px}footer{padding:60px 60px;background-color:#222;background-color:var(--mainBlack);color:#fff;color:var(--mainWhite);display:grid;line-height:3rem;width:100%;grid-gap:20px;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr))}footer p{line-height:2rem}@media screen and (max-width: 768px){footer{padding:60px 30px}}footer a{color:#cfcfcf;line-height:3rem}.footer-card a{color:white}.input{outline:none;background-color:transparent;border:1px solid #f7f7f7;border:1px solid var(--offWhite);width:90%;padding:15px 20px;color:white}.subscribe-btn{width:90%;padding:20px;color:white;background-color:rgb(255, 111, 255);background-color:var(--primaryColor);margin-top:20px;border:0;cursor:pointer;outline:none}

:root{--primaryColor:  rgb(155, 2, 155);--secondaryColor:  rgb(255, 111, 255);--mainWhite: #fff;--offWhite: #f7f7f7;--mainBlack: #222;--mainGrey: #ececec;--darkGrey: #cfcfcf;--mainTransition: all 0.3s linear;--mainSpacing: 3px;--lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);--darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5)}a{text-decoration:none;color:#222;color:var(--mainBlack)}.about{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));width:90vw;grid-gap:15px;padding:20px;align-items:center;justify-content:center;margin:30px auto}.about p{line-height:2rem}.about-image img{width:100%;height:300px;object-fit:cover}.icons{width:60px;height:60px}.card{box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);transition:0.3s;background-color:#fff;background-color:var(--mainWhite);width:90%;height:90%;line-height:2rem;margin:0px auto;padding:40px 30px;z-index:5}.contact-card{box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);background-color:#fff;background-color:var(--mainWhite);transition:0.3s;line-height:2rem;width:90%;min-height:90%;margin:0px auto;padding:30px 30px;z-index:2}.card:hover{box-shadow:0 8px 16px 0 rgba(0,0,0,0.2);background-color:rgb(155, 2, 155);background-color:var(--primaryColor);color:#fff;color:var(--mainWhite)}.card:hover:first-child{background-color:rgb(255, 111, 255);background-color:var(--secondaryColor);color:#222;color:var(--mainBlack)}.contact-card:hover:first-child{background-color:rgb(255, 111, 255);background-color:var(--secondaryColor);color:#222;color:var(--mainBlack)}.contact-card h3{margin-bottom:10px}.social *{margin-left:5px;font-size:12px}.social-icon{display:flex;align-items:center;margin-bottom:15px}.social a:hover{color:rgb(155, 2, 155);color:var(--primaryColor)}.vision{width:100%;margin:20px auto}

